body {
    & .all {
        & .information-container {
            display          : flex;
            justify-content  : space-around;
            background       : rgba(29, 34, 62, 0.99);
            padding          : 8rem 0;
            box-shadow       : 0 0 10px 3px rgba(29, 34, 62, 0.55);

            & .img-contact img {
                max-height : 245px;
                border     : 4px solid rgb(242, 76, 76);
            }
    
            & .information-txt {
                display : flex;
    
                & .info-icons {
                    display        : flex;
                    margin         : 0 2rem 0 auto;
                    flex-direction : column;
                    background     : rgb(242, 76, 76);
                    max-height     : 245px;
                    border-radius  : 0 0 10px 10px;
    
                    & svg {
                        margin       : 2rem 1rem 0 1rem;
                        display      : block;
                    }
    
                    & :first-child {
                        margin-top: 1.5rem;
                    }
    
                    & :last-child {
                        margin-bottom: .5rem;
                    }
                }
    
                & .info-txt {
                    & p {
                        color      : white;
                        font-size  : 1.5rem;
                        margin     : 2rem 0;
                    }
                    & :first-child {
                        margin-top: 1.5rem;
                    }
                    & :last-child {
                        margin-bottom: .5rem;
                    }
                }
            }
        }
    }
}