@media only screen and (max-width: 1600px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                         margin : 8rem 10rem 0 0;
                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 1380px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                         margin : 8rem 5rem 0 0;
                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 1240px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                         margin : 8rem 3rem 0 0;
                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 860px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                        margin : 15rem 3rem 1rem 0;
                        & h1 {
                            margin-bottom: 2rem;
                        }

                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 650px) {
    body {
        & #root {
            & .all {
                & .information-contact {
                  & .information-container {
                    display: block;
                    text-align: left;
                    & .information-txt {
                        & .info-icons {
                            margin: -0.25rem 2rem 0 0;
                        }
                    }
                  }  
                }
            }
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                        margin : 20rem 3rem 1rem 0;
                        & h1 {
                            text-align: center;
                            margin-bottom: 15rem;
                        }
                        & .aboutMe {
                            text-align: center;
                            margin-top : 3rem;
                        }

                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 608px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {

                        & h1 {
                            font-size: 4rem;
                            margin-bottom : 7rem;
                        }
                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 570px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                    & .img-accueil {
                        position  : absolute;
                        top       : 3rem;
                        left      : 0%;
                        right     : 0%;
                        & img {
                            border     : 4px solid rgb(242, 76, 76); 
                             
                        }
                    }

                     & .accueil-container {
                        margin : 25rem 3rem 1rem 0;
                        & h1 {
                            font-size    : 4rem;
                            margin        : 0;
                            margin-bottom : 5rem;
                        }
                     }
                 }
             }
            & .exp-page {
                & .exp-txt {
                    & .exp-container {
                        & .exp-main {
                            padding: 10px;
                            text-align: left;
                            display: block;
                            & .exp-content {
                                min-width       : 0px; 
                            }
                        }
                    }
                }
            }
        } 
     }
}
@media only screen and (max-width: 500px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                    & .img-accueil {

                    }

                     & .accueil-container {
                        & h1 {
                            font-size: 2.5rem;
                        }
                        & .aboutMe a {
                            font-size: 1.5rem;
                        }
                     }
                 }
             }
        } 
     }
}
@media only screen and (max-width: 450px) {
    body {
        & #root {
            & .all {
                & .information-contact {
                  & .information-container {
                    display: block;
                    text-align: left;
                    & .information-txt {
                        & .info-icons {
                            margin: -0.25rem 2rem 0 0;
                        }
                        & .info-txt {
                            font-size: 1rem;
                        }
                    }
                  }  
                }
            }
        } 
     }
}
@media only screen and (max-width: 450px) {
    body {
        & #root {
            & .all {
                & .information-contact {
                  & .information-container {
                    & .information-txt {
                        & .info-icons {
                        }
                        & .info-txt p {
                            font-size: 1rem;
                        }
                    }
                  }  
                }
            }
        } 
     }
}
@media only screen and (max-width: 330px) {
    body {
        & #root {
             & .accueil {
                 & .accueil-first {
                     & .accueil-container {
                        & h1 {
                            font-size: 2rem;
                        }
                        & .aboutMe a {
                            font-size: 1rem;
                        }
                     }
                 }
             }
        } 
     }
}
